@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400&display=swap');

$main-font-stack: 'Nunito', sans-serif;
$code-font-stack: source-code-pro, Menlo, Monaco, Consolas,
  'Courier New', monospace;

$big: 40px;
$medium: 22px;

$left-arrows: polygon(
  40% 0%,
  40% 25%,
  100% 25%,
  100% 75%,
  40% 75%,
  40% 100%,
  0 50%
);
